*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%;box-sizing:border-box;font-size:1rem}@media (max-width: 1200px){html{font-size:1rem}}@media (max-width: 900px){html{font-size:1rem}}@media (max-width: 600px){html{font-size:0.8rem}}:root{--color-grey: rgba(97, 125, 152, 0.8);--clr-pink: #f28ab2}body{background-color:var(--clr-pink)}.container{background-color:#fff;display:flex;flex-direction:column;width:32.5vw;margin:5rem auto;padding:2rem}@media (max-width: 1200px){.container{width:50vw}}@media (max-width: 900px){.container{width:60vw}}@media (max-width: 600px){.container{width:80vw}}.friends__container{display:flex;flex-direction:column;gap:1.5rem}.friend{display:flex;gap:1.2rem;align-items:center}.friend__img{width:5rem;height:5rem;border-radius:50%;object-fit:cover}.friend .details{display:flex;flex-direction:column;letter-spacing:1px;gap:0.5rem}.friend .details__name{font-family:"Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;font-weight:900;font-size:1.1rem}.friend .details__age{color:var(--color-grey)}.btn{background-color:var(--clr-pink);color:#fff;text-align:center;padding:0.7rem 3rem;border-radius:4px;font-size:1.4rem;letter-spacing:2px;cursor:pointer}
